:root {
  --font-primary: "Barlow", sans-serif;
  --font-secondary: "Fraunces", serif;
}

:root {
  --softred: hsl(7, 99%, 70%);
  --yellow: hsl(51, 100%, 49%);
  --darkdesaturatedcyan: hsl(167, 40%, 24%);
  --darkblue: hsl(198, 62%, 26%);
  --darkmoderatecyan: hsl(168, 34%, 41%);
  --verydarkdesaturatedblue: hsl(212, 27%, 19%);
  --verydarkgrayishblue: hsl(213, 9%, 39%);
  --darkgrayishblue: hsl(232, 10%, 55%);
  --grayishblue: hsl(210, 4%, 67%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}
html:focus-within {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-size: 18px;
  font-family: var(--font-primary);
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 90rem;
}
@media (min-width: 1200px) {
  .container {
    max-width: fit-content;
  }
}

.filter-green {
  filter: invert(27%) sepia(59%) saturate(361%) hue-rotate(116deg) brightness(45%) contrast(92%);
}

footer {
  background-color: #90d4c5;
}
footer img {
  margin: 0 auto;
  padding: 4rem 0;
  padding-bottom: 3rem;
}
footer .footer-links {
  display: flex;
  margin: 0;
  margin-bottom: 1rem;
  justify-content: center;
}
footer .footer-links > * {
  margin: 0 2rem;
  color: var(--darkdesaturatedcyan);
  transition: all 250ms ease-in-out;
}
footer .footer-links > *:hover {
  color: var(--white);
}
footer .footer-icons {
  display: flex;
  justify-content: center;
}
footer .footer-icons > * {
  margin: 0 1rem;
  transition: all 250ms ease-in-out;
}
footer .footer-icons > *:hover {
  filter: invert(99%) sepia(99%) saturate(0%) hue-rotate(360deg) brightness(110%) contrast(110%);
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}
.attribution a:hover {
  color: var(--softred);
}

header {
  position: relative;
  height: 100vh;
  background-image: url(../app/images/desktop/image-header.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 599px) {
  header {
    height: 60vh;
    background-image: url(../app/images/mobile/image-header.jpg);
  }
}
header h1 {
  font-size: 3rem;
  font-weight: 900;
  letter-spacing: 8px;
  text-align: center;
  color: var(--white);
  text-transform: uppercase;
  font-family: var(--font-secondary);
}
@media (max-width: 599px) {
  header h1 {
    font-size: 2.5rem;
    position: relative;
    top: -65px;
    left: 0;
  }
}
@media (min-width: 600px) {
  header h1 {
    position: relative;
    top: -90px;
    left: 0;
    font-size: 2.2rem;
  }
}
header nav {
  display: flex;
  justify-content: space-between;
}
header nav .header-logo {
  margin: 2rem;
}
@media (max-width: 599px) {
  header nav .header-logo {
    margin: 1rem;
  }
}
header nav .burger-menu-icon {
  display: flex;
  align-items: center;
  margin-right: 2rem;
  cursor: pointer;
}
@media (max-width: 599px) {
  header nav .burger-menu-icon {
    margin-right: 1rem;
  }
}
@media (min-width: 900px) {
  header nav .burger-menu-icon {
    display: none;
  }
}
header nav #links {
  display: none;
  justify-content: space-evenly;
  align-items: center;
}
@media (min-width: 900px) {
  header nav #links {
    display: flex;
  }
}
header nav #links a {
  color: var(--white);
  margin: 0 1.5rem;
}
header nav #links a:last-child {
  margin-right: 2rem;
  background-color: var(--white);
  border-radius: 50px;
  padding: 1rem 2rem;
  text-transform: uppercase;
  color: var(--verydarkdesaturatedblue);
  font-family: var(--font-secondary);
  font-size: 1rem;
  transition: all 250ms ease-in-out;
}
header nav #links a:last-child:hover {
  background-color: #70d2ff;
  color: var(--white);
}
header nav #burger-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 10%;
  left: 50%;
  min-width: 90vw;
  background-color: var(--white);
  transform: translateX(-50%);
  border-top: 50px solid #3ebfff;
  border-right: 50px solid var(--white);
  z-index: 1;
}
@media (max-width: 599px) {
  header nav #burger-menu {
    min-width: 90vw;
    top: 17%;
  }
}
@media (min-width: 600px) {
  header nav #burger-menu {
    top: 15%;
  }
}
header nav #burger-menu > * {
  margin: 1rem 0;
  margin-left: 2.5rem;
}
@media (min-width: 600px) {
  header nav #burger-menu > * {
    margin: 1rem 0;
    margin-left: 2.5rem;
  }
}
header nav #burger-menu a {
  color: var(--grayishblue);
}
header nav #burger-menu a:last-child {
  background-color: var(--yellow);
  border-radius: 50px;
  padding: 1rem 2rem;
  text-transform: uppercase;
  color: var(--verydarkdesaturatedblue);
  font-family: var(--font-secondary);
  font-size: 1rem;
}
header .arrow {
  position: relative;
  top: 36%;
  left: 50%;
}
@media (max-width: 599px) {
  header .arrow {
    top: 43%;
  }
}
@media (min-width: 600px) {
  header .arrow {
    top: 23%;
  }
}
@media (min-width: 1200px) {
  header .arrow {
    top: 33%;
  }
}

.misc {
  display: grid;
}
@media (max-width: 599px) {
  .misc {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, minmax(150px, 300px));
    grid-template-areas: "milk oranges" "icecream sugar";
  }
}
@media (min-width: 600px) {
  .misc {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 300px);
    grid-template-areas: "milk oranges" "icecream sugar";
  }
}
@media (min-width: 1200px) {
  .misc {
    grid-template-columns: repeat(4, minmax(280px, 360px));
    grid-template-rows: 450px;
    grid-template-areas: "milk oranges icecream sugar";
  }
}
.misc .milk {
  grid-area: milk;
  background-image: url(../app/images/desktop/image-gallery-milkbottles.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.misc .orange {
  grid-area: oranges;
  background-image: url(../app/images/desktop/image-gallery-orange.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.misc .icecream {
  grid-area: icecream;
  background-image: url(../app/images/desktop/image-gallery-cone.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.misc .sugar {
  grid-area: sugar;
  background-image: url(../app/images/desktop/image-gallery-sugarcubes.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.t-header {
  text-transform: uppercase;
  font-family: var(--font-secondary);
  text-align: center;
  letter-spacing: 5px;
  word-spacing: 5px;
  color: var(--verydarkgrayishblue);
  margin-top: 7rem;
}
@media (min-width: 600px) {
  .t-header {
    margin-bottom: 0;
  }
}
@media (max-width: 599px) {
  .t-header {
    margin-top: 4rem;
  }
}

.testimonials {
  display: grid;
  justify-content: center;
  align-content: center;
  height: 80vh;
}
@media (max-width: 599px) {
  .testimonials {
    height: 100%;
    grid-template-columns: minmax(375px, 599px);
    grid-template-rows: repeat(3, auto);
    margin-bottom: 2rem;
  }
}
@media (min-width: 600px) {
  .testimonials {
    height: 100%;
    grid-template-columns: minmax(375px, 599px);
    grid-template-rows: repeat(3, auto);
    margin-bottom: 2rem;
  }
}
@media (min-width: 900px) {
  .testimonials {
    grid-template-columns: repeat(3, minmax(375px, 450px));
    grid-template-rows: repeat(4, auto);
    margin-bottom: 2rem;
  }
}
.testimonials img {
  border-radius: 50%;
  width: 15%;
  margin: 0 auto;
  margin-top: 4rem;
}
.testimonials .quote {
  text-align: center;
  margin: 5rem auto;
  width: 78%;
  color: var(--verydarkdesaturatedblue);
}
@media (max-width: 599px) {
  .testimonials .quote {
    font-size: 1rem;
    margin: 2rem auto;
  }
}
.testimonials .name {
  text-align: center;
  font-family: var(--font-secondary);
  font-weight: 900;
  color: var(--verydarkdesaturatedblue);
}
.testimonials .role {
  text-align: center;
  color: var(--grayishblue);
}

.wrapper {
  display: grid;
}
@media (max-width: 599px) {
  .wrapper {
    grid-template-columns: minmax(375px, 599px);
    grid-template-rows: 350px 450px 350px 450px 600px 600px;
    grid-template-areas: "egg" "transform" "glass" "standout" "design" "photography";
  }
}
@media (min-width: 600px) {
  .wrapper {
    grid-template-columns: minmax(600px, 899px);
    grid-template-rows: 350px 450px 350px 450px 600px 600px;
    grid-template-areas: "egg" "transform" "glass" "standout" "design" "photography";
  }
}
@media (min-width: 900px) {
  .wrapper {
    grid-template-columns: repeat(2, minmax(300px, 720px));
    grid-template-rows: repeat(3, 600px);
    grid-template-areas: "transform egg" "glass standout" "design photography";
  }
}
.wrapper .transform {
  grid-area: transform;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 7rem;
  color: var(--verydarkdesaturatedblue);
}
@media (min-width: 600px) {
  .wrapper .transform {
    margin: 0;
    align-items: center;
  }
}
@media (max-width: 599px) {
  .wrapper .transform {
    margin: 0;
    justify-content: center;
  }
}
.wrapper .transform h2 {
  font-family: var(--font-secondary);
  font-weight: 900;
  font-size: 2.5rem;
  width: 25rem;
  line-height: 1.2;
}
@media (min-width: 600px) {
  .wrapper .transform h2 {
    font-size: 2rem;
    text-align: center;
    margin: 0;
    width: 80%;
  }
}
@media (max-width: 599px) {
  .wrapper .transform h2 {
    font-size: 2rem;
    text-align: center;
    margin: 0 3rem;
    width: 80%;
  }
}
.wrapper .transform p {
  margin: 3rem 0;
  font-size: 1.2rem;
}
@media (min-width: 600px) {
  .wrapper .transform p {
    margin: 3rem 2rem;
  }
}
@media (max-width: 599px) {
  .wrapper .transform p {
    font-size: 1.1rem;
    margin: 3rem 2rem;
    text-align: center;
  }
}
.wrapper .transform a {
  text-transform: uppercase;
  font-family: var(--font-secondary);
  font-size: 1rem;
  letter-spacing: 1px;
  color: var(--verydarkdesaturatedblue);
}
.wrapper .transform a:hover + .highlight {
  opacity: 1;
}
@media (max-width: 599px) {
  .wrapper .transform a {
    text-align: center;
  }
}
.wrapper .transform .highlight {
  position: relative;
  top: -10px;
  left: 0;
  content: "";
  width: 122px;
  border-bottom: solid 10px var(--yellow);
  border-radius: 5px;
  opacity: 0.4;
  z-index: -1;
}
@media (max-width: 599px) {
  .wrapper .transform .highlight {
    left: 50%;
    transform: translateX(-50%);
  }
}
.wrapper .egg {
  grid-area: egg;
  background-image: url(../app/images/desktop/image-transform.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 599px) {
  .wrapper .egg {
    background-image: url(../app/images/mobile/image-transform.jpg);
  }
}
.wrapper .standout {
  grid-area: standout;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  margin: 0 7rem;
  color: var(--verydarkdesaturatedblue);
}
@media (min-width: 600px) {
  .wrapper .standout {
    margin: 0;
    align-items: center;
  }
}
@media (max-width: 599px) {
  .wrapper .standout {
    margin: 0;
    justify-content: center;
  }
}
.wrapper .standout h2 {
  font-family: var(--font-secondary);
  font-weight: 900;
  font-size: 2.5rem;
  width: 30rem;
  line-height: 1.2;
}
@media (min-width: 600px) {
  .wrapper .standout h2 {
    font-size: 2rem;
    text-align: center;
    margin: 0;
    width: 80%;
  }
}
@media (max-width: 599px) {
  .wrapper .standout h2 {
    font-size: 2rem;
    text-align: center;
    margin: 0 3rem;
    width: 80%;
  }
}
.wrapper .standout p {
  margin: 3rem 0;
  font-size: 1.2rem;
}
@media (min-width: 600px) {
  .wrapper .standout p {
    margin: 3rem 2rem;
  }
}
@media (max-width: 599px) {
  .wrapper .standout p {
    font-size: 1.1rem;
    margin: 3rem 2rem;
    text-align: center;
  }
}
.wrapper .standout a {
  text-transform: uppercase;
  font-family: var(--font-secondary);
  font-size: 1rem;
  letter-spacing: 1px;
  color: var(--verydarkdesaturatedblue);
}
.wrapper .standout a:hover + .highlight {
  opacity: 1;
}
@media (max-width: 599px) {
  .wrapper .standout a {
    text-align: center;
  }
}
.wrapper .standout .highlight {
  position: relative;
  top: -10px;
  left: 0;
  content: "";
  width: 122px;
  border-bottom: solid 10px var(--softred);
  border-radius: 5px;
  opacity: 0.4;
  z-index: -1;
}
@media (max-width: 599px) {
  .wrapper .standout .highlight {
    left: 50%;
    transform: translateX(-50%);
  }
}
.wrapper .glass {
  grid-area: glass;
  background-image: url(../app/images/desktop/image-stand-out.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 599px) {
  .wrapper .glass {
    background-image: url(../app/images/mobile/image-stand-out.jpg);
  }
}
.wrapper .design {
  grid-area: design;
  background-image: url(../app/images/desktop/image-graphic-design.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: var(--darkdesaturatedcyan);
}
.wrapper .design h3 {
  font-family: var(--font-secondary);
}
@media (min-width: 600px) {
  .wrapper .design h3 {
    margin: 1rem;
  }
}
@media (max-width: 599px) {
  .wrapper .design h3 {
    margin: 1rem;
  }
}
.wrapper .design p {
  margin: 3rem 10rem;
}
@media (min-width: 600px) {
  .wrapper .design p {
    padding: 0;
    margin: 1rem 2rem;
    font-size: 1rem;
    margin-bottom: 4rem;
  }
}
@media (max-width: 599px) {
  .wrapper .design p {
    padding: 0;
    margin: 1rem;
    font-size: 1rem;
    margin-bottom: 4rem;
  }
}
.wrapper .photography {
  grid-area: photography;
  background-image: url(../app/images/desktop/image-photography.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: var(--darkblue);
}
.wrapper .photography h3 {
  font-family: var(--font-secondary);
}
@media (min-width: 600px) {
  .wrapper .photography h3 {
    margin: 1rem;
  }
}
@media (max-width: 599px) {
  .wrapper .photography h3 {
    margin: 1rem;
  }
}
.wrapper .photography p {
  margin: 3rem 10rem;
}
@media (min-width: 600px) {
  .wrapper .photography p {
    padding: 0;
    margin: 1rem 3rem;
    font-size: 1rem;
    margin-bottom: 4rem;
  }
}
@media (max-width: 599px) {
  .wrapper .photography p {
    padding: 0;
    margin: 1rem;
    font-size: 1rem;
    margin-bottom: 4rem;
  }
}